import AlignmentButton from './components/alignmentButton';
import AlignmentLeftButton from './components/alignmentLeftButton';
import AlignmentCenterButton from './components/alignmentCenterButton';
import AlignmentRightButton from './components/alignmentRightButton';
import AlignmentJustifyButton from './components/alignmentJustify';

export default function toolbar(alignmentConfig) {
  const alignmentLocaleConfig = alignmentConfig.locale || {};
  return {
    buttons: {
      default: AlignmentButton,
      left: AlignmentLeftButton,
      center: AlignmentCenterButton,
      justify: AlignmentJustifyButton,
      right: AlignmentRightButton,
    },
    locale: alignmentLocaleConfig,
  };
}
